import React, { Component } from "react";
import {
  Page,
  List,
  ListHeader,
  ListItem,
  Icon,
  ProgressBar
} from "react-onsenui";
import ActivityPage from "../ActivityPage/ActivityPage";
import NavBar from "../NavBar/NavBar";
import "./ActivityListPage.css";
import axios from "axios";

const LOCATION_QUERY_URI = "https://cors-anywhere.herokuapp.com/http://159.203.3.180/api/locations/";
const PROGRESS_QUERY_URI = "https://cors-anywhere.herokuapp.com/http://159.203.3.180/api/users/activities";
const PROGRESS_UPDATE_URI = "https://cors-anywhere.herokuapp.com/http://159.203.3.180/api/users/activities/";

class ActivityListPage extends Component {
  constructor(props) {
    super(props);

    this.state = {
      activities: [],
      activitiesCompleted: [],
      isLoading: false,
      errors: ""
    };

    this.renderRow = this.renderRow.bind(this);
    // this.setDone = this.setDone.bind(this);
    this.renderThumbnail = this.renderThumbnail.bind(this);
    this.pushPage = this.pushPage.bind(this);
    this.fetchActivityList = this.fetchActivityList.bind(this);
    this.fetchActivityProgress = this.fetchActivityProgress.bind(this);
    this.setActivityDone = this.setActivityDone.bind(this);
  }

  setActivityDone(id) {
    const updatedProgress = [...this.state.activitiesCompleted, id];

    const headers = {
      Authorization: this.props.token
    };

    axios
      .post(`${PROGRESS_UPDATE_URI}${id}`, {}, { headers })
      .then(res => {
        this.setState({ activitiesCompleted: updatedProgress });
        const activitiesIDs = this.state.activities.map(item => item._id);
        const areAllActivitiesDone = activitiesIDs.every(v =>
          updatedProgress.includes(v)
        );
        if (areAllActivitiesDone) {
          this.props.setLocationDone(this.props.locationId);
        }
        console.log(res.data);
      })
      .catch(err => {
        console.log(err.response.data);
      });
  }

  // componentWillUnmount() {
  //   this.props.resetState();
  // }

  renderThumbnail(activityType) {
    if (activityType === "photo") {
      return (
        <Icon
          icon="camera"
          size={32}
          className="list-item__thumbnail"
          style={{ color: "#FABC39", height: "auto", width: 10 }}
        />
      );
    } else if (activityType === "multiple-choice-question") {
      return (
        <Icon
          icon="list-alt"
          size={32}
          className="list-item__thumbnail"
          style={{ color: "#F17D80", height: "auto", width: 10 }}
        />
      );
    } else if (activityType === "short-answer") {
      return (
        <Icon
          icon="question-circle"
          size={32}
          className="list-item__thumbnail"
          style={{ color: "#AB19DB", height: "auto", width: 10 }}
        />
      );
    } else if (activityType === "qr-code") {
      return (
        <Icon
          icon="qrcode"
          size={32}
          className="list-item__thumbnail"
          style={{ color: "#68A8AD", height: "auto", width: 10 }}
        />
      );
    }
  }

  // setDone(id) {
  //   const newActivities = this.state.activities.map(row => {
  //     if (row.activityID === id) row.isDone = true;
  //     return row;
  //   });

  //   console.log(newActivities);

  //   this.setState({
  //     activities: newActivities
  //   });
  // }

  pushPage(id) {
    this.props.navigator.pushPage({
      component: ActivityPage,
      props: {
        activity: this.state.activities.find(item => item._id === id),
        isDone: this.state.activitiesCompleted.includes(id) ? true : null,
        setActivityDone: this.setActivityDone,
        setLocationDone: this.props.setLocationDone,
        locationId: this.props.locationId,
        locationName: this.state.locationName,
        key: `ACTIVITY_PAGE_${Date.now()}`,
      }
    });
  }

  fetchActivityList() {
    this.setState({ isLoading: true });

    const headers = {
      Authorization: this.props.token
    };

    const URI = `${LOCATION_QUERY_URI}${this.props.locationId}`;

    axios
      .get(URI, { headers })
      .then(res =>
        this.setState({
          activities: res.data.activities,
          locationName: res.data.title,
          isLoading: false
        })
      )
      .catch(err => {
        this.setState({
          errors: err.response,
          isLoading: false
        });
      });
  }

  fetchActivityProgress() {
    this.setState({ isLoading: true });

    const headers = {
      Authorization: this.props.token
    };

    axios
      .get(PROGRESS_QUERY_URI, { headers })
      .then(res =>
        this.setState({
          activitiesCompleted: res.data.activitiesCompleted,
          isLoading: false
        })
      )
      .catch(err => {
        this.setState({
          errors: err.response,
          isLoading: false
        });
      });
  }

  componentDidMount() {
    this.fetchActivityList();

    if (this.state.activitiesCompleted.length === 0) {
      this.fetchActivityProgress();
    }
  }

  renderRow(row, index) {
    return (
      <ListItem
        key={row._id}
        modifier={
          index === this.state.activities.length - 1 ? "longdivider" : null
        }
        onClick={() => this.pushPage(row._id)}
        tappable
      >
        <div
          className="left ActivityListPage-thumbnail"
          style={{ minWidth: 60 }}
        >
          {/* <img src={row.image} className='list-item__thumbnail LocationsPage-thumbnail' /> */}
          {this.renderThumbnail(row.type)}
          {this.state.activitiesCompleted.includes(row._id) && (
            <Icon
              icon="check-circle"
              size={24}
              className="ActivityListPage-checkmark"
            />
          )}
        </div>
        <div className="center ActivityListPage-task_title">
          {row.activityName}
        </div>
        <div className="right">
          <Icon icon="fa-chevron-right" style={{ color: "#CCC" }} />
        </div>
      </ListItem>
    );
  }

  render() {
    return (
      <Page
        renderToolbar={() => (
          <NavBar
            title="Activities"
            navigator={this.props.navigator}
            icon={<Icon icon="tasks" />}
            backButton
          />
        )}
      >
        {this.state.isLoading && <ProgressBar indeterminate />}
        <List
          dataSource={this.state.activities}
          renderRow={this.renderRow}
          renderHeader={() => (
            <ListHeader style={{ textAlign: "center" }}>
              {this.state.locationName}
            </ListHeader>
          )}
        />
      </Page>
    );
  }
}

export default ActivityListPage;
