import React, { Component } from "react";
import { Page, List, ListItem, Icon, ProgressBar } from "react-onsenui";
import NavBar from "../NavBar/NavBar";
import LocationPage from "../LocationPage/LocationPage";
import axios from "axios";
import "./LocationsPage.css";

const LOCATIONS_QUERY_URI = "https://cors-anywhere.herokuapp.com/http://159.203.3.180/api/locations";
const PROGRESS_QUERY_URI = "https://cors-anywhere.herokuapp.com/http://159.203.3.180/api/users/locations";
const PROGRESS_UPDATE_URI = "https://cors-anywhere.herokuapp.com/http://159.203.3.180/api/users/locations/";

class LocationsPage extends Component {
  constructor(props) {
    super(props);

    this.state = {
      locations: [],
      locationsCompleted: [],
      isLoading: false,
      errors: ""
    };

    this.handleLocationSelection = this.handleLocationSelection.bind(this);
    this.renderRow = this.renderRow.bind(this);
    this.fetchLocations = this.fetchLocations.bind(this);
    this.fetchLocationsProgress = this.fetchLocationsProgress.bind(this);
    this.setLocationDone = this.setLocationDone.bind(this);
  }

  fetchLocations() {
    this.setState({ isLoading: true });

    const headers = {
      Authorization: this.props.token
    };

    axios
      .get(LOCATIONS_QUERY_URI, { headers })
      .then(res =>
        this.setState({
          locations: res.data,
          isLoading: false
        })
      )
      .catch(err => {
        localStorage.removeItem("scavengerHuntTkn");
        this.setState({
          errors: err.response,
          isLoading: false
        });
      });
  }

  fetchLocationsProgress() {
    this.setState({ isLoading: true });

    const headers = {
      Authorization: this.props.token
    };

    axios
      .get(PROGRESS_QUERY_URI, { headers })
      .then(res =>
        this.setState({
          locationsCompleted: res.data.locationsCompleted,
          isLoading: false
        })
      )
      .catch(err => {
        this.setState({
          errors: err.response,
          isLoading: false
        });
      });
  }

  setLocationDone(id) {
    const updatedProgress = [...this.state.locationsCompleted, id];

    const headers = {
      Authorization: this.props.token
    };

    axios
      .post(`${PROGRESS_UPDATE_URI}${id}`, {}, { headers })
      .then(res => {
        this.setState({ locationsCompleted: updatedProgress });
        console.log(res.data);
      })
      .catch(err => {
        console.log(err.response.data);
      });
  }

  componentDidMount() {
    this.fetchLocations();

    if (!this.state.locationsCompleted.length) {
      this.fetchLocationsProgress();
    }
  }

  handleLocationSelection(id) {
    this.props.navigator.pushPage({
      component: LocationPage,
      props: {
        location: this.state.locations.find(item => item._id === id),
        token: this.props.token,
        key: `LOCATION_PAGE_${Date.now()}`,
        setLocationDone: this.setLocationDone
      }
    });
  }

  renderRow(row, index) {
    return (
      <ListItem
        key={row._id}
        modifier={
          index === this.state.locations.length - 1 ? "longdivider" : null
        }
        onClick={() => {
          this.handleLocationSelection(row._id);
        }}
        tappable
      >
        <div className="left" style={{ minWidth: 60, maxWidth: 60 }}>
          <img
            src={row.image}
            className="list-item__thumbnail LocationsPage-thumbnail"
            alt=""
          />
          {this.state.locationsCompleted.includes(row._id) && (
            <Icon
              icon="check-circle"
              size={24}
              className="LocationsPage-checkmark"
            />
          )}
        </div>
        <div className="center">
          <div className="list-item__title">{row.title}</div>
          <div className="list-item__subtitle">{row.subtitle}</div>
        </div>
        <div className="right">
          <Icon icon="fa-chevron-right" style={{ color: "#CCC" }} />
        </div>
      </ListItem>
    );
  }

  render() {
    return (
      <Page
        renderToolbar={() => (
          <NavBar
            title="Locations"
            navigator={this.props.navigator}
            icon={<Icon icon="map-marker-alt" />}
          />
        )}
      >
        {this.state.isLoading && <ProgressBar indeterminate />}
        <List dataSource={this.state.locations} renderRow={this.renderRow} />
      </Page>
    );
  }
}

export default LocationsPage;
