import React, { Component } from "react";
import { Icon } from "react-onsenui";
import "./PhotoTask.css";
import axios from "axios";

class PhotoTask extends Component {
  constructor(props) {
    super(props);

    this.state = {
      pictureURL: null,
      isDone: this.props.isDone
    }

    this.handleChange = this.handleChange.bind(this);
  }

  handleChange(event) {
    const data = new FormData();
    data.append("image", event.target.files[0]);
    data.append("album", "3OYirzt");
    data.append("title", `${this.props.locationName} - ${this.props.activity.activityName}`);

    const headers = {
      Authorization: "Bearer 2c43803a6844c641fd484634c4c591d355c4aaef",
      "Content-Type": "multipart/form-data"
    };

    axios
      .post("https://api.imgur.com/3/image", data, { headers })
      .then(res => {
        console.log(res.data);
        this.setState({
          pictureURL: res.data.data.link,
          isDone: true
        })
      })
      .catch(err => {
        console.log(err.response.data);
      });
  }

  componentDidUpdate() {
    if (this.state.isDone) {
      this.props.setActivityDone(this.props.activity._id);
    }
  }

  render() {
    const activity = this.props.activity;

    return (
      <div className="PhotoTask-wrapper">
        {this.state.isDone &&
          <div className="PhotoTask-completed">
            <Icon icon='check' size={28} style={{ color: '#FFF' }} />&nbsp;&nbsp;Completed!
        </div>}
        <div className="PhotoTask-description_wrapper">
          <span className="PhotoTask-activity_name">
            {activity.activityName}
          </span>
          <p className="PhotoTask-activity_description">{activity.activityDescription}</p>
        </div>
        {!this.state.isDone &&
          <div className="PhotoTask-camera_wrapper">
            <label className="PhotoTask-camera_label">
              <Icon icon='camera' size={72} style={{ color: '#FABC39' }} />
              <input style={{ display: 'none' }} type="file" accept="image/*;capture=camera" onChange={this.handleChange} />
            </label>
          </div>
        }
        {this.state.pictureURL &&
          <div>
            <img src={this.state.pictureURL} style={{width: "100%"}} alt="" />
          </div>
        }
      </div>

    );
  }
}

export default PhotoTask;