import React, { Component } from "react";
import * as ons from 'onsenui';
import { Icon, Card } from "react-onsenui";
import QrReader from 'react-qr-reader';
import "./ScanQRCodeTask.css";

class ScanQRCodeTask extends Component {
  constructor(props) {
    super(props);

    this.state = {
      displayCamera: null,
      isDone: this.props.isDone
    };

    this.displayCamera = this.displayCamera.bind(this);
  }

  displayCamera() {
    this.setState({
      displayCamera: true
    });
  }

  handleScan = data => {
    const activity = this.props.activity;
    if (data) {
      if (data === activity.content) {
        this.setState({
          isDone: true,
          displayCamera: false,
        });
      } else {
        ons.notification.alert({
          message: 'Sorry, seems like you scanned a wrong QR code!',
          title: "Ooops"
        });
      }
    }
  }

  handleError = err => {
    console.error(err)
  }

  componentDidUpdate() {
    if (this.state.isDone) {
      this.props.setActivityDone(this.props.activity._id);
    }
  }

  render() {
    const activity = this.props.activity;

    return (
      <div className="PhotoTask-wrapper">
        {this.state.isDone && (
          <div className="PhotoTask-completed">
            <Icon icon="check" size={28} style={{ color: "#FFF" }} />
            &nbsp;&nbsp;Completed!
          </div>
        )}
        <div className="PhotoTask-description_wrapper">
          <span className="PhotoTask-activity_name">
            {activity.activityName}
          </span>
          <p className="PhotoTask-activity_description">
            {activity.activityDescription}
          </p>
        </div>
        {!this.state.isDone ? (
          <div>
            <QrReader
              delay={1000}
              onError={this.handleError}
              onScan={this.handleScan}
              style={{ width: '100%' }}
            />
          </div>
        ) : (
          <Card>
            <div className="title center">QR Code Content</div>
            <div className="content center" style={{fontSize: 16}}>
              {activity.content}
            </div>
          </Card>
        )}
      </div>
    );
  }
}

export default ScanQRCodeTask;
