import React, { Component } from "react";
import { Page, Icon } from "react-onsenui";
import NavBar from "../NavBar/NavBar";
import "./ActivityPage.css";
import QuestionTask from "../QuestionTask/QuestionTask";
import PhotoTask from "../PhotoTask/PhotoTask";
import ShortAnswerTask from "../ShortAnswerTask/ShortAnswerTask";
import ScanQRCodeTask from "../ScanQRCodeTask/ScanQRCodeTask";

class ActivityPage extends Component {
  constructor(props) {
    super(props);
    
    this.state = {
      selectedAnswer: null,
      picture: null,
      pictureURL: null
    };
  }

  // componentWillUnmount() {
  //   this.props.setDone(this.props.id);
  // }
  
  render() {
    const activity = this.props.activity;

    return (
      <Page
        renderToolbar={() => (
          <NavBar
            title="Task"
            navigator={this.props.navigator}
            icon={<Icon icon="thumbtack" />}
            backButton
          />
        )}
      >
        {activity.type === "photo" && <PhotoTask {...this.props} />}
        {activity.type === "multiple-choice-question" && <QuestionTask {...this.props} />}
        {activity.type === "short-answer" && <ShortAnswerTask {...this.props} />}
        {activity.type === "qr-code" && <ScanQRCodeTask {...this.props} />}
      </Page>
    );
  }
}

export default ActivityPage;
