import React, { Component } from "react";
import { Icon, Input, Button } from "react-onsenui";
import "./ShortAnswerTask.css";

class ShortAnswerTask extends Component {
  constructor(props) {
    super(props);

    this.state = {
      isCorrect: this.props.isDone,
      answer: ""
    };

    this.handleClick = this.handleClick.bind(this);
  }

  handleClick() {
    if (this.state.isCorrect) return;

    const activity = this.props.activity;
    const answers = activity.answers.map(item => item.toLowerCase());

    this.setState({ isCorrect: answers.includes(this.state.answer.toLowerCase()) });
  }

  componentDidUpdate() {
    if (this.state.isCorrect) {
      this.props.setActivityDone(this.props.activity._id);
    }
  }

  render() {
    const activity = this.props.activity;

    return (
      <div className="ShortAnswerTask-wrapper">
        {this.state.isCorrect && (
          <div className="ShortAnswerTask-completed">
            <Icon icon="check" size={28} style={{ color: "#FFFFFF" }} />
            &nbsp;&nbsp;Completed!
          </div>
        )}
        {this.state.isCorrect === false && (
          <div className="ShortAnswerTask-wrong">
            <Icon
              icon="ShortAnswerTask"
              size={28}
              style={{ color: "#FFFFFF" }}
            />
            &nbsp;&nbsp;Try again!
          </div>
        )}
        <div className="ShortAnswerTask-description_wrapper">
          <span className="ShortAnswerTask-activity_name">
            {activity.activityName}
          </span>
          <p className="ShortAnswerTask-activity_description">
            {activity.activityDescription}
          </p>
        </div>
        <div className="ShortAnswerTask-answer_wrapper">
          <div>

            <Input
              className="ShortAnswerTask-input"
              value={this.state.isCorrect ? activity.answers[0] : this.state.answer}
              float
              onChange={event => {
                this.setState({ answer: event.target.value });
              }}
              
              type="text"
              placeholder="Your answer"
              readOnly={this.state.isCorrect ? true : false}
            />
          </div>
          <Button
            className="ShortAnswerTask-button"
            modifier='cta'
            onClick={this.handleClick}
            disabled={this.state.isCorrect ? true : false}
          >
            Check
          </Button>
        </div>
      </div>
    );
  }
}

export default ShortAnswerTask;
