import React, { Component } from "react";
import { Page, Input, Button } from "react-onsenui";
import ErrorMsg from "../ErrorMsg/ErrorMsg";
import axios from "axios";
import "./SignUpPage.css";

const SIGNUP_URI = "https://cors-anywhere.herokuapp.com/http://159.203.3.180/api/users/register";

class SignUpPage extends Component {
  constructor(props) {
    super(props);

    this.state = {
      name: "",
      email: "",
      password: "",
      password2: "",
      errors: []
    };

    this.handleRegistration = this.handleRegistration.bind(this);
  }

  handleRegistration() {
    // TO-DO (client-side validation)
    const { errors, ...payload } = this.state;

    axios
      .post(SIGNUP_URI, payload)
      .then(res => this.props.navigator.popPage())
      .catch(err => this.setState({ errors: err.response.data }));
  }

  render() {
    return (
      <Page>
        <div className="LoginPage">
          <img src="sask-polytech-logo-compressed.png" width="200" alt="Saskatchewan Polytechnic" />
          <section className="LoginPage-form">
            <p className="LoginPage-title">Sign Up</p>
            <Input
              value={this.state.name}
              onChange={e => this.setState({ name: e.target.value })}
              type="text"
              disable-auto-styling
              placeholder="Name"
              className="LoginPage-input"
            />
            <Input
              value={this.state.email}
              onChange={e => this.setState({ email: e.target.value })}
              type="email"
              disable-auto-styling
              placeholder="Email"
              className="LoginPage-input"
            />
            <Input
              value={this.state.password}
              onChange={e => this.setState({ password: e.target.value })}
              type="password"
              disable-auto-styling
              placeholder="Password"
              className="LoginPage-input"
            />
            <Input
              value={this.state.password2}
              onChange={e => this.setState({ password2: e.target.value })}
              type="password"
              disable-auto-styling
              placeholder="Repeat password"
              className="LoginPage-input"
            />
            {this.state.errors &&
              this.state.errors.map((err, index) => {
                return <ErrorMsg key={index} message={err} />;
              })}
            <Button
              modifier="large"
              className="SignUpPage-register_btn"
              onClick={this.handleRegistration}
            >
              CREATE ACCOUNT
            </Button>
            <Button
              modifier="large--quiet"
              className="LoginPage-signup_btn"
              onClick={() => this.props.navigator.popPage()}
            >
              I've got an account
            </Button>
          </section>
          <img
            src="team-logo-white.png"
            width="100"
            alt="oowee Entertainment"
          />
        </div>
      </Page>
    );
  }
}

export default SignUpPage;
