import React from "react";
import { Toolbar, BackButton, Button, Icon } from "react-onsenui";
import "./NavBar.css";
import * as ons from "onsenui";
import LoginPage from "../LoginPage/LoginPage";

const signOut = navigator => {
  ons.notification
    .confirm("Are you sure you want to log out?", {
      buttonLabels: ["Yes", "No"]
    })
    .then(response => {
      if (response === 0) {
        localStorage.removeItem("scavengerHuntTkn");
        navigator.resetPage({
          component: LoginPage,
          props: { key: `LOGIN_PAGE${Date.now()}` }
        });
      }
    });
};

const NavBar = ({ title, navigator, icon, backButton }) => (
  <Toolbar className="NavBar">
    <div className="left NavBar-left">
      {backButton ? <BackButton onClick={() => navigator.popPage()} /> : null}
    </div>
    <div className="center NavBar-center">
      {icon} {title}
    </div>
    <div className="right">
      <Button modifier="large--quiet" onClick={() => signOut(navigator)}>
        <Icon className="SignOut-icon" icon="fa-sign-out-alt" />
      </Button>
    </div>
  </Toolbar>
);

export default NavBar;
