import React, { Component } from "react";
import { Icon, Button } from "react-onsenui";
import "./QuestionTask.css";

const COLOURS = ["#E3423C", "#D89E28", "#1268CE", "#458F0F", "#B87341"];

class QuestionTask extends Component {
  constructor(props) {
    super(props);

    this.state = {
      isCorrect: this.props.isDone
    };

    this.handleClick = this.handleClick.bind(this);
  }

  handleClick(optionId) {
    if (this.state.isCorrect) return;
    
    const selection = this.props.activity.options.find(
      option => option._id === optionId
    );

    this.setState({ isCorrect: selection.correct });
  }

  componentDidUpdate() {
    if (this.state.isCorrect) {
      this.props.setActivityDone(this.props.activity._id);
    }
  }

  render() {
    const activity = this.props.activity;

    return (
      <div className="QuestionTask-wrapper">
        {this.state.isCorrect && (
          <div className="QuestionTask-completed">
            <Icon icon="check" size={28} style={{ color: "#FFFFFF" }} />
            &nbsp;&nbsp;Completed!
          </div>
        )}
        {this.state.isCorrect === false && (
          <div className="QuestionTask-wrong">
            <Icon icon="check" size={28} style={{ color: "#FFFFFF" }} />
            &nbsp;&nbsp;Try again!
          </div>
        )}
        <div className="QuestionTask-description_wrapper">
          <span className="QuestionTask-activity_name">
            {activity.activityName}
          </span>
          <p className="QuestionTask-activity_description">
            {activity.activityDescription}
          </p>
        </div>
        {activity.options.map((option, index) => {
          const colour = COLOURS[index];
          return (
            <Button
              key={option._id}
              modifier="large--cta"
              className="QuestionTask-button"
              style={(this.state.isCorrect && !option.correct) ? {} : { backgroundColor: colour }}
              onClick={() => this.handleClick(option._id)}
              disabled={(this.state.isCorrect && !option.correct) ? true : false}
            >
              {option.answer}
            </Button>
          );
        })}
      </div>
    );
  }
}

export default QuestionTask;
